export const gherkin = {
  name: "gherkin",
  startState: function () {
    return {
      lineNumber: 0,
      tableHeaderLine: false,
      allowFeature: true,
      allowBackground: false,
      allowScenario: false,
      allowSteps: false,
      allowPlaceholders: false,
      allowMultilineArgument: false,
      inMultilineString: false,
      inMultilineTable: false,
      inKeywordLine: false
    };
  },
  token: function (stream, state) {
    if (stream.sol()) {
      state.lineNumber++;
      state.inKeywordLine = false;
      if (state.inMultilineTable) {
        state.tableHeaderLine = false;
        if (!stream.match(/\s*\|/, false)) {
          state.allowMultilineArgument = false;
          state.inMultilineTable = false;
        }
      }
    }

    stream.eatSpace();

    if (state.allowMultilineArgument) {

      // STRING
      if (state.inMultilineString) {
        if (stream.match('"""')) {
          state.inMultilineString = false;
          state.allowMultilineArgument = false;
        } else {
          stream.match(/.*/);
        }
        return "string";
      }

      // TABLE
      if (state.inMultilineTable) {
        if (stream.match(/\|\s*/)) {
          return "bracket";
        } else {
          stream.match(/[^\|]*/);
          return state.tableHeaderLine ? "header" : "string";
        }
      }

      // DETECT START
      if (stream.match('"""')) {
        // String
        state.inMultilineString = true;
        return "string";
      } else if (stream.match("|")) {
        // Table
        state.inMultilineTable = true;
        state.tableHeaderLine = true;
        return "bracket";
      }

    }

    // LINE COMMENT
    if (stream.match(/#.*/)) {
      return "comment";

      // TAG
    } else if (!state.inKeywordLine && stream.match(/@\S+/)) {
      return "tag";

      // FEATURE
    } else if (!state.inKeywordLine && state.allowFeature && stream.match(/(|||||||||| || |||||||||||||||Waciwo|Vlastnos|Trajto|Tnh nng|Savyb|Pretty much|Poiadavka|Poadavek|Potrzeba biznesowa|zellik|Osobina|Ominaisuus|Omadus|OH HAI|Mogunost|Mogucnost|Jellemz|Hwt|Hwaet|Funzionalit|Funktionalitit|Funktionalitt|Funkcja|Funkcionalnost|Funkcionalitte|Funkcia|Fungsi|Functionaliteit|Funcionalitate|Funcionalitate|Functionalitate|Funcionalitat|Funcionalidade|Fonctionnalit|Fitur|Fa|Feature|Eiginleiki|Egenskap|Egenskab|Caracterstica|Caracteristica|Business Need|Aspekt|Arwedd|Ahoy matey!|Ability):/)) {
      state.allowScenario = true;
      state.allowBackground = true;
      state.allowPlaceholders = false;
      state.allowSteps = false;
      state.allowMultilineArgument = false;
      state.inKeywordLine = true;
      return "keyword";

      // BACKGROUND
    } else if (!state.inKeywordLine && state.allowBackground && stream.match(/(|||||||||||||||||||Zaoenia|Yo\-ho\-ho|Tausta|Taust|Situcija|Rerefons|Pozadina|Pozadie|Pozad|Osnova|Latar Belakang|Kontext|Konteksts|Kontekstas|Kontekst|Httr|Hannergrond|Grundlage|Gemi|Fundo|Fono|First off|Dis is what went down|Dasar|Contexto|Contexte|Context|Contesto|Cenrio de Fundo|Cenario de Fundo|Cefndir|Bi cnh|Bakgrunnur|Bakgrunn|Bakgrund|Baggrund|Background|B4|Antecedents|Antecedentes|r|Aer|Achtergrond):/)) {
      state.allowPlaceholders = false;
      state.allowSteps = true;
      state.allowBackground = false;
      state.allowMultilineArgument = false;
      state.inKeywordLine = true;
      return "keyword";

      // SCENARIO OUTLINE
    } else if (!state.inKeywordLine && state.allowScenario && stream.match(/(|||||||| |||||  | | | | | | | | | | ||  || |Wharrimean is|Template Situai|Template Senario|Template Keadaan|Tapausaihio|Szenariogrundriss|Szablon scenariusza|Swa hwr swa|Swa hwaer swa|Struktura scenarija|Structur scenariu|Structura scenariu|Skica|Skenario konsep|Shiver me timbers|Senaryo tasla|Schema dello scenario|Scenariomall|Scenariomal|Scenario Template|Scenario Outline|Scenario Amlinellol|Scenrijs pc parauga|Scenarijaus ablonas|Reckon it's like|Raamstsenaarium|Plang vum Szenario|Plan du Scnario|Plan du scnario|Osnova scne|Osnova Scenra|Nrt Scenru|Nrt Scne|Nrt Scenra|MISHUN SRSLY|Menggariskan Senario|Lsing Dma|Lsing Atburarsar|Konturo de la scenaro|Koncept|Khung tnh hung|Khung kch bn|Forgatknyv vzlat|Esquema do Cenrio|Esquema do Cenario|Esquema del escenario|Esquema de l'escenari|Esbozo do escenario|Delineao do Cenrio|Delineacao do Cenario|All y'all|Abstrakt Scenario|Abstract Scenario):/)) {
      state.allowPlaceholders = true;
      state.allowSteps = true;
      state.allowMultilineArgument = false;
      state.inKeywordLine = true;
      return "keyword";

      // EXAMPLES
    } else if (state.allowScenario && stream.match(/(|||||||||| ||||||||||||You'll wanna|Voorbeelden|Variantai|Tapaukset|Se e|Se the|Se e|Scenarios|Scenariji|Scenarijai|Przykady|Primjeri|Primeri|Pklady|Prklady|Piemri|Pldk|Pavyzdiai|Paraugs|rnekler|Juhtumid|Exemplos|Exemples|Exemple|Exempel|EXAMPLZ|Examples|Esempi|Enghreifftiau|Ekzemploj|Eksempler|Ejemplos|D liu|Dead men tell no tales|Dmi|Contoh|Cenrios|Cenarios|Beispiller|Beispiele|Atburarsir):/)) {
      state.allowPlaceholders = false;
      state.allowSteps = true;
      state.allowBackground = false;
      state.allowMultilineArgument = true;
      return "keyword";

      // SCENARIO
    } else if (!state.inKeywordLine && state.allowScenario && stream.match(/(|||||||||||||||||||Tnh hung|The thing of it is|Tapaus|Szenario|Swa|Stsenaarium|Skenario|Situai|Senaryo|Senario|Scenaro|Scenariusz|Scenariu|Scnario|Scenario|Scenarijus|Scenrijs|Scenarij|Scenarie|Scn|Scenr|Primer|MISHUN|Kch bn|Keadaan|Heave to|Forgatknyv|Escenario|Escenari|Cenrio|Cenario|Awww, look mate|Atburars):/)) {
      state.allowPlaceholders = false;
      state.allowSteps = true;
      state.allowBackground = false;
      state.allowMultilineArgument = false;
      state.inKeywordLine = true;
      return "keyword";

      // STEPS
    } else if (!state.inKeywordLine && state.allowSteps && stream.match(/(||||||||||||||||||||||||||||| | | | | | | | | | | | | |  | | | | |  | | | | | | | | | | | | | | | | | | | | | |  | | | | | | | | | | | | | | | | | | |,  | | | | | | | | | | | | |   | | | | | | | | | | | | | | | | | | | |  | | | | | | |urh |egar |a e | |a |Zatati |Zakadajc |Zadato |Zadate |Zadano |Zadani |Zadan |Za pedpokladu |Za predpokladu |Youse know when youse got |Youse know like when |Yna |Yeah nah |Y'know |Y |Wun |Wtedy |When y'all |When |Wenn |WEN |wann |Ve |V |Und |Un |ugeholl |Too right |Thurh |Th |Then y'all |Then |Tha the |Tha |Tetapi |Tapi |Tak |Tada |Tad |Stel |Soit |Siis |i |i |Si |Sed |Se |S |Quando |Quand |Quan |Pryd |Potom |Pokud |Pokia |Per |Pero |Pak |Oraz |Onda |Ond |Oletetaan |Og |Och |O zaman |Niin |Nhng |Nr |Nr |Mutta |Men |Mas |Maka |Majd |Majc |Mais |Maar |m |Ma |Lorsque |Lorsqu'|Logo |Let go and haul |Kun |Kuid |Kui |Kiedy |Khi |Ketika |Kemudian |Ke |Kdy |Kaj |Kai |Kada |Kad |Jeeli |Jeli |Ja |It's just unbelievable |Ir |I CAN HAZ |I |Ha |Givun |Givet |Given y'all |Given |Gitt |Gegeven |Gegeben seien |Gegeben sei |Gdy |Gangway! |Fakat |tant donns |Etant donns |tant donnes |Etant donnes |tant donne |Etant donne |tant donn |Etant donn |Et |s |Entonces |Entn |Ento |Entao |En |Eer ki |Ef |Eeldades |E |urh |Duota |Dun |Donitao |Donat |Donada |Do |Diyelim ki |Diberi |Dengan |Den youse gotta |DEN |De |Dato |Dai fiind |Dai fiind |Dati fiind |Dati |Date fiind |Date |Data |Dat fiind |Dar |Dann |dann |Dan |Dados |Dado |Dadas |Dada |a e |a |Cuando |Cho |Cando |Cnd |Cand |Cal |But y'all |But at the end of the day I reckon |BUT |But |Buh |Blimey! |Bit |Bet |Bagi |Aye |awer |Avast! |Atunci |Atesa |Ats |Apabila |Anrhegedig a |Angenommen |And y'all |And |AN |An |an |Amikor |Amennyiben |Ama |Als |Alors |Allora |Ali |Aleshores |Ale |Akkor |Ak |Adott |Ac |Aber |A zrove |A tie |A taktie |A tak |A |a |7 |\* )/)) {
      state.inStep = true;
      state.allowPlaceholders = true;
      state.allowMultilineArgument = true;
      state.inKeywordLine = true;
      return "keyword";

      // INLINE STRING
    } else if (stream.match(/"[^"]*"?/)) {
      return "string";

      // PLACEHOLDER
    } else if (state.allowPlaceholders && stream.match(/<[^>]*>?/)) {
      return "variable";

      // Fall through
    } else {
      stream.next();
      stream.eatWhile(/[^@"<#]/);
      return null;
    }
  }
};

